// 変数の定義
// 960 1040 1120 1200 1280
$container-width: 1300px;

// breakpoint
$screen-size:(
xs:400,
sm:500,
md:768,
lg:1000,
xl:1200,
)!default;

html {
    font-size: 10px;
    word-wrap: break-word;
    font-family: arial, sans-serif;
}

body {
    font-size: 1.4rem;
}

div,
section,
article,
nav,
aside {
    box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5 {
    font-size: 1.6rem;
    font-weight: normal;
    margin: 0;
    display: block;
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

ol {
    list-style: none;
    padding: 0;
    margin: 0;
}

li {
    margin: 0;
    padding: 0;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

a {
    text-decoration: none;
}

span {
    display: inline-block;
}

// @mixinの定義
@mixin mq-up($breakpoint: md) {
    $screen-size: #{map-get($screen-size, $breakpoint)}px;
    @media screen and (min-width: #{$screen-size}) {
        @content;
    }
}

@mixin mq-down($breakpoint: md) {
    $screen-size: #{map-get($screen-size, $breakpoint) - 1};
    @media screen and (max-width: #{$screen-size}px) {
        @content;
    }
}

