
body {
    background-color: #000000;
    // -webkit-text-size-adjust: 100%;
}

.wrapper {}

.header {
    margin-bottom: 27px;
    @include mq-up(lg) {
        margin-bottom: 136px;
    }
}

.hero {
    &__image {
        width: 100%;
        &--pc {
            display: none;
            @include mq-up(lg) {
                display: block;
            }
        }
        &--sp {
            display: block;
            @include mq-up(lg) {
                display: none;
            }
        }
    }
}

.content {
    max-width: 1300px;
    margin: auto;
    @include mq-down(lg) {
        padding: 0 20px;
    }
}

.title {
    border-bottom: 6px solid #d6d6d6;
    position: relative;
    padding: 12px 0;
    color: #15f1f2;
    font-size: 6.4rem;
    font-weight: bold;
    @include mq-down(lg) {
        font-size: 4.8rem;
    }
    &:after {
        content: "";
        display: block;
        position: absolute;
        bottom: -6px;
        left: 0;
        background-color: #15f1f2;
        height: 6px;
        width: 20%;
    }
}

.game {
    margin: 20px auto 30px;
    @include mq-up(lg) {
        margin: 137px auto 108px;
        display: flex;
    }
    &__image {
        @include mq-up(lg) {
            width: 50%;
        }
        img {
            width: 100%;
        }
    }
    &__description {
        color: #fff;
        font-size: 3.2rem;
        padding: 20px 0;
        @include mq-down(lg) {
            font-size: 2.4rem;
            font-weight: bold;
        }
        @include mq-up(lg) {
            width: 50%;
            padding: 20px 30px;
        }
    }
    &__catch {
        font-size: 4.8rem;
        margin-bottom: 1.5em;
        font-weight: bold;
        @include mq-down(lg) {
            font-size: 2.4rem;
        }
    }
}

@keyframes attention {
    0% {
        color: #fff;
    }
    50% {
        color: #000;
    }
    100% {
        color: #fff;
    }
}

.gameInfo {
    margin: 40px 0 50px;
    @include mq-up(lg) {
      margin: 70px 0;
    }
    &__text {
      color: #fff;
      font-size: 4.8rem;
      font-weight: bold;
      animation: attention 3s linear 0s infinite normal;
      @include mq-down(lg) {
          font-size: 2.8rem;
      }
    }
}

.stage {
    @include mq-up(lg) {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-bottom: 132px;
    }
    &__item {
        @include mq-down(lg) {
            margin-bottom: 60px;
        }
        @include mq-up(lg) {
            width: 30%;
        }
    }
}

.card {
    &__title {
        font-weight: bold;
        font-size: 4.8rem;
        color: #fff;
        margin-bottom: 5px;
        @include mq-down(lg) {
            font-size: 2.6rem;
            margin-bottom: 15px;
        }
    }
    &__image {
        margin-bottom: 40px;
        img {
            width: 100%;
        }
    }
    &__description {
        color: #fff;
        font-size: 3.2rem;
        @include mq-down(lg) {
            font-size: 2.4rem;
        }
    }

}

.sealed {
    margin: 30px 0 60px;
    &__title {
      font-size: 4.8rem;
      font-weight: bold;
      color: #fff;
      @include mq-down(lg) {
        font-size: 2.8rem;
      }
    }
    &__word {
        display: flex;
        margin: 20px 0;
    }
    &__input {
        max-width: 400px;
        background-color: #fff;
        font-size: 4.6rem;
        padding: 0.3em;
        line-height: 1;
        color: #666;
      @include mq-down(lg) {
        font-size: 2.8rem;
      }
    }
    &__button  {
      min-width: 100px;
      padding: 0.3em;
      font-size: 4.6rem;
      background: #c4c4c4;
      @include mq-down(lg) {
        font-size: 2.8rem;
      }
      &:hover {
          opacity: 0.8;
      }
    }
    &__notice {
      font-size: 4.1rem;
      color: #fff;
      @include mq-down(lg) {
        font-size: 2.8rem;
      }
    }
}

.aboutUs {
    margin-top: 48px;
    display: flex;
    flex-wrap: wrap;
    @include mq-down(lg) {
        margin-top: 26px;
    }
    &__description {
        flex: 1 1 60%;
        color: #fff;
        font-size: 3.2rem;
        @include mq-down(lg) {
            font-size: 2.2rem;
        }
        @include mq-up(lg) {
            padding-right: 85px;
        }
    }
    &__image {
        max-width: 500px;
        img {
            width: 100%;
        }
    }
}

.history {
    margin: 2em 0;
    font-size: 3.2rem;
    @include mq-down(lg) {
        font-size: 1.9rem;
    }
    color: #fff;
    &__title {}
    &__list {}
    &__item {
        display: flex;
        @include mq-down(lg) {
            &:before {
                content: "・";
            }
        }
    }
    &__date {}
    &__text {
        margin-left: 1em;
    }
}

.footer {
    margin-top: 120px;
    padding-bottom: 20px;
    text-align: center;
    font-size: 1.6rem;
    color: #fff;
}
